import React, { Component } from 'react';
import DatePicker from 'react-mobile-datepicker';
import './FinanceAnalysis.scss';
import {convertDate} from "react-mobile-datepicker/lib/time";


class FinanceAnalysis extends Component{
  state = {
    time: new Date(),
    isOpen: false,
    formOpen: true,
    buttonShape: '+'
  };

  // convertDate = (date, format) => {
  //   const year = date.getFullYear();
  //   const month = date.getMonth() + 1;
  //   const day = date.getDate();
  //   const hour = date.getHours();
  //   const minute = date.getMinutes();
  //   const second = date.getSeconds();
  //
  //   return format
  //     .replace(/Y+/, year)
  //     .replace(/M+/, month)
  //     .replace(/D+/, day)
  //     .replace(/h+/, hour)
  //     .replace(/m+/, minute)
  //     .replace(/s+/, second);
  // };

  handleClick = () => {
    this.setState({ isOpen: true });
  };

  handleCancel = () => {
    this.setState({ isOpen: false });
  };

  handleSelect = (time) => {
    let date = convertDate(time, 'YYYY-MM-DD');
    alert(`취득 일자 : ${date}`);
    this.setState({ time, isOpen: false });
  };

  showForm = () => {
    if(this.state.formOpen) {
      this.setState({
        formOpen: !this.state.formOpen,
        buttonShape: '-'
      });
    } else {
      this.setState({
        formOpen: !this.state.formOpen,
        buttonShape: '+'
      })
    }
  };

  testAlert = () => {
    alert('for test');
  };

  render() {
    let state = this.state;
    return (
      <div className="main-container">
        <div className="form-info__title">
          <div>기본정보</div>
          <button onClick={this.showForm}>{state.buttonShape}</button>
        </div>
        <div id="basic-form" className="form-info" hidden={state.formOpen}>
          <div>보유하고 계신 주택의 현재까지의 투자상태를 알아보기 위해 가정...</div>
          <input id="address" type="text" placeholder="주소입력"/>
          <div>
            <input type="radio"/>
            KB 시세
            <input type="text" placeholder="59,000만원 (2019.01월 기준)"/>
          </div>
          <div>
            <input type="radio"/>
            직접 입력
            <input type="text" placeholder="숫자만 입력 가능 (단위: 만원)"/>
          </div>
          <button onClick={this.testAlert}>다음</button>
        </div>
        <div className="acquire-date">
          <button
            className="select-btn"
            onClick={this.handleClick}
          >
            select time
          </button>
          <DatePicker
            value={state.time}
            isOpen={state.isOpen}
            theme={'dark'}
            onSelect={this.handleSelect}
            onCancel={this.handleCancel}
            confirmText={'확인'}
            cancelText={'취소'}
            showHeader={true}
          />
        </div>
      </div>
    )
  }
}


export default FinanceAnalysis;
