import React, { Component } from 'react';
import './KakaoTemplate.scss';
import logo from "shared/zipfund.jpeg";

class KakaoTemplate extends Component {
  // state = {
  //   addKakao: ''
  // };
  //
  componentDidMount() {
    this.addPlusFriend();
  }

  addPlusFriend = () =>  {
    window.Kakao.PlusFriend.createAddFriendButton({
      container: '.button__plusfriend',
      plusFriendId: '_UizlC' // 플러스친구 홈 URL에 명시된 id로 설정합니다.
    });
  };

  render() {
    window.Kakao.init('13685f3473427aa71101892bfb1c1e70');

    return (
      <div className="main-container">
        <img src={logo} className="App-logo" alt="logo" />
        <button className="App-link">
          Zip Advisor
        </button>
        <button className="button__plusfriend"/>
      </div>
    )
  }
}

export default KakaoTemplate;
